import React, { useState } from 'react';
import PropTypes from 'prop-types';

// projects
import { Stack, Tab, Tabs, styled } from '@mui/material';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && children}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired
};

function a11yProps(index) {
  return {
    id: `tab-${index}`,
    'aria-controls': `tabpanel-${index}`
  };
}
const tabHeight = 38;

const TabsWrapper = styled(Tabs, {
  shouldForwardProp: (prop) => prop !== 'direction' && prop !== 'totaltabs'
})(({ theme, direction, totaltabs }) => {
  return {
    height:
      (direction === 'vertical' &&
        'calc(totaltabs * (tabHeight + theme.spacing(2))))') ||
      38,
    minHeight: tabHeight,
    maxHeight: 460,
    overflow: 'visible',
    '& .MuiTabs-flexContainer': {
      flexDirection: direction === 'vertical' ? 'column' : 'row',
      alignItems: 'flex-start',
      gap: theme.spacing(2)
    },
    '& .MuiTabs-indicator': {
      height: tabHeight,
      minHeight: tabHeight,
      width: '100%',
      border: '1px solid' + theme.palette.primary.dark,
      borderRadius: theme.shape.borderRadius,
      boxShadow: '0px 2px 10px ' + +theme.palette.primary.light
    },
    '& .MuiTabs-scrollableX': {
      overflowX: 'auto !important'
    }
  };
});

const TabWrapper = styled(Tab)(({ theme }) => ({
  padding: 0,
  height: tabHeight,
  minHeight: tabHeight,
  maxWidth: 180,
  borderRadius: theme.shape.borderRadius,
  transition: 'color .2s',
  textTransform: 'capitalize',
  '&.MuiButtonBase-root': {
    minWidth: 'auto',
    paddingLeft: 20,
    paddingRight: 20,
    marginRight: 4
  },
  '&.Mui-selected, &.Mui-selected:hover': {
    color: theme.palette.grey[50],
    zIndex: 5
  },
  '&:hover': {
    color: theme.palette.grey[200]
  }
}));

const CustomizedTabs = (props) => {
  const { tabs, orientation } = props;
  const [currentTab, setCurrentTab] = useState(tabs[0].value);

  const handleTabsChange = (event, value) => {
    setCurrentTab(value);
  };

  return (
    <Stack
      direction={orientation === 'vertical' ? 'row' : 'column'}
      spacing={{ xs: 1, sm: 2, md: 3 }}
    >
      <TabsWrapper
        onChange={handleTabsChange}
        orientation={orientation}
        value={currentTab}
        variant="scrollable"
        scrollButtons="auto"
        textColor="primary"
        indicatorColor="primary"
        selectionFollowsFocus
        direction={orientation}
        totaltabs={tabs.length}
      >
        {tabs.map((tab) => (
          <TabWrapper
            key={tab.value}
            icon={tab.icon?.component}
            iconPosition={tab.icon?.position}
            label={tab.label}
            value={tab.value}
            wrapped
            {...a11yProps(tab.value)}
          />
        ))}
      </TabsWrapper>
      {tabs.map((tab) => (
        <TabPanel key={tab.value} value={currentTab} index={tab.value}>
          {currentTab === tab.value && tab.panel}
        </TabPanel>
      ))}
    </Stack>
  );
};

CustomizedTabs.propTypes = {
  children: PropTypes.node,
  tabs: PropTypes.array.isRequired,
  orientation: PropTypes.oneOf(['vertical', 'horizontal'])
};

export default CustomizedTabs;
